import React, { Component } from "react";
import emailjs from "emailjs-com";

class ContactMe extends Component {
  sendEmail = (content) => {
    const YOUR_TEMPLATE_ID = "template_u7S7m5pW";
    const YOUR_USER_ID = "user_jDtoR1wzdDLo7ecxyJY0U";
    const YOUR_SERVICE_ID = "gmail";

    content.preventDefault();
    console.log(content.target);

    emailjs
      .sendForm(YOUR_SERVICE_ID, YOUR_TEMPLATE_ID, content.target, YOUR_USER_ID)
      .then(
        (result) => {
          console.log(result.text);
        },
        (error) => {
          console.log(error.text);
        }
      );
  };
  render() {
    return (
      <React.Fragment>
        <form method="POST" className="contact-form" onSubmit={this.sendEmail}>
          <label>Name</label>
          <input type="text" name="from_name" />
          <label>Email</label>
          <input type="email" name="reply_to" />
          <label>Message</label>
          <textarea name="message" />
          <input type="submit" value="Send" />
          <br></br>
          <div
            class="g-recaptcha"
            data-sitekey="6LeScfoUAAAAAIBBpGFLIkYYgf5cAxN5lpa5vCGI"
          ></div>
        </form>
      </React.Fragment>
    );
  }
}

export default ContactMe;
